@media (max-width: 500px) {
  .main {
    position: relative;
  }
  .main_form {
    width: 60%;
    margin: 20px auto;

    display: flex;
    flex-direction: column;
    align-items: center;
  }
  input {
    min-width: 100px;
    padding: 10px;
    width: 100%;
    font-size: 1.2rem;
    margin: 20px 0;
  }
  #buttonType {
    width: 80%;
    font-size: 1.2rem;
  }
}
